import React, {useRef, useState} from "react"
import styled from "styled-components"
import Button from "../components/Button"
import useStores from "../store"
import {useHistory } from 'react-router-dom'
const Wrapper =styled.div`
background-color: #fff;
padding: 40px;
margin-left: auto;
margin-right: auto;
width:460px;
display: flex;
flex-direction: column;
align-items: center;
border: 1px dashed #ccc;
p{
text-align: center;
font-size: 18px;
font-weight: bold;
}
label{
display: flex;
margin-bottom: 16px;
justify-content: center;
align-items: center;

span{
display: inline-block;
font-size: 12px;
width: 6em;
margin-right: 20px;
}
.note{
padding-left: 10px;
color: orangered;
}
input{
padding: 4px;
border: 1px solid #ccc;
border-radius: 4px;
&:focus{
border: 1px solid dodgerblue;
}
}
}
`;
function Register() {
    const [username,setUsername]=useState('')
    const [confirm,setConfirm]=useState('')
    const [password,setPassword]=useState('')
    const [messageName,setMessageName]=useState('用户名应为4-10的字符')
    const [messagePassword,setMessagePassword]=useState('密码应为8位字符')
    const [messageConfirm,setMessageConfirm]=useState('')
    const refName =useRef(null)
    const refPassword =useRef(null)
    const refConfirm =useRef(null)
    const history =useHistory();
     const {AuthStore}=useStores()
    const submit=()=> {
        if (messageName === 'bingo!' && messageConfirm === 'bingo!') {
            AuthStore.setUsername(username)
            AuthStore.setPassword(confirm)
            AuthStore.register().then(
                () => {
                    history.push('/')
                },
                () => {
                    console.log('注册失败')
                }
            )
        }
    }
    const enterRegister=(event)=>{
        console.log('xxx')
        if(event.key === 'Enter'){
            submit()
        }
    }
const changeConfirm=()=>{
    if (refPassword.current !== null){
        const value = refConfirm.current.value.replace(' ', '');
        if(value===''){
            setMessageConfirm('密码不能为空')
            return
        }
        if(value!==password){
            setMessageConfirm('密码输入不一致')
            return
        }
            setConfirm(value)
            setMessageConfirm('bingo!')

    }
}
    const changePassword=()=> {
        if (refPassword.current !== null) {
            const value = refPassword.current.value.replace(' ', '');
            const length = value.length;
            if (length === 0) {
                setMessagePassword('密码不能为空')
                return
            }
            if (length !== 8) {
                setMessagePassword('密码长度不是8位')
                return
            }
            setPassword(value)
            setMessagePassword('bingo!')
        }
    }
 const changeName=()=>{
     if(refName.current!==null) {
         const value= refName.current.value.replace(' ','');
           refName.current.value=value
         const length =value.length;
         if (length === 0) {
             setMessageName('用户名不能为空')
             return
         }
         if (length > 10) {
             setMessageName('用户名长度不能超过10')
             return
         }
         if (length < 4) {
             setMessageName('用户名长度不能小于4')
             return
         }
         setUsername(value)
         setMessageName('bingo!')
     }
 }
    return(
        <Wrapper>
            <p>注册</p>
            <label name='useName'>
                <span>用户名:</span>
                <input
                    type='text' defaultValue={username}
                    ref={refName}
                    onBlur={changeName}
                    onKeyPress={enterRegister}
                />
                <span className='note'> {messageName}</span>
            </label>
            <label name='password'>
                <span>密码:</span>
                <input type='password' defaultValue={password}
                       ref={refPassword}
                       onBlur={changePassword}
                       onKeyPress={enterRegister}
                />
                <span className='note'>{messagePassword}</span>
            </label>
            <label name='confirm'>
                <span>确认密码:</span>
                <input type='password'
                       defaultValue={confirm}
                       ref={refConfirm}
                       onBlur={changeConfirm}
                       onKeyPress={enterRegister}
                />
                <span className='note'>{messageConfirm}</span>
            </label>
             <Button onClick={submit}>注册</Button>
        </Wrapper>
    )
}
export default Register;